 *{
    padding: 0;
    margin: 0;
}
.hr{
    border: none;
    background-color: rgb(220, 220, 245);
    height: 2px;
    /* margin-top: 15px; */
    width: 450px;
    border: 0;
    /* height: 3px; */
    /* background-image: linear-gradient(to right, rgba(7, 8, 46, 25), rgba(85, 77, 189, 0), rgba(7, 8, 46, 25)); */
