@import url('https://fonts.googleapis.com/css2?family=PT+Serif&display=swap');
*{
    padding: 0;
    margin: 0;
}
.about-main{
    height: 100vh;
    display: grid;
    place-items: center;
    background-color:rgba(0, 0, 0,.65);
    
}
.about-main  h1{
    color: #fff;
}
.about-main-iner-1{
    /* background-color: rebeccapurple; */
    height: auto;
    width: 850px;
    margin: 0 auto;   
}
.about-main-iner-1 p{
    font-family: monospace;
    color: whitesmoke;
}
.about-para{
    height: auto;
    display: grid;
    font-family: 'PT Serif', serif;
    font-size: 19px;
    justify-content: space-between;
    /* grid-template-rows: 1fr 1fr 1fr 1fr 1fr; */
}
@media (max-width:900px){
    .about-main{
        height: auto;
    }
    .about-main-iner-1{
    width:90%;
    }
    .about-para{
        display: flex;
        flex-direction: column;
        gap: 25px;
        padding: 10px;
    }
    .about-main p{
        letter-spacing: -1.2px;
    }
    
}