@import url('https://fonts.googleapis.com/css2?family=Space+Mono&display=swap');
*{
    padding: 0;
    margin: 0;
}


.project-main{
    /* background-color: #0C0C0C; */
    background-color:rgba(0, 0, 0,.7);
    padding: 15px;
}

.projects{
    max-width: 1350px;
    margin: 0 auto;
    height: 86vh;
    /* background-color: #174ca2; */
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    justify-content: space-between;
    align-items: center;
}
video{
    /* width: 650px;
    height: 450px; */
    cursor: pointer;
}
.project-main h1{
    text-align: center;
    padding: 25px;
    font-family: 'Share Tech Mono';
    font-size: 50px;
    color: #174ca2;
}


.project-1{
    display: flex;
    max-width: 100%;
    gap: 25px;
    background-color: #0c0c0c;
    align-items:center;
    justify-content: space-around ;
}
.project-inner h2{
    color: #dd7365;
    font-weight: 700;
    font-family: 'Share Tech Mono';
}
.project-inner p {
    color: aliceblue;
    font-size: 18px;
    /* font-family: 'Space Mono', monospace; */
    font-family: 'Noto Sans Mono', monospace;

}
.project-inner{
    max-width: 850px;
}
span{
    color: orange;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 700;
}
@media (max-width:900px) {
    .project-main{
        /* width */
    }
    .projects{
        height: auto;
        gap: 35px;
    }
        .project-1{
            flex-direction: column;
        }
        .project-1 img{
            width: 350px;
        }
        .project-1 video{
            width: 350px;
        }
        .project-inner{
            width: 90%;
        }
        .project-inner span{
            width: 50px;
        }
        .project-inner p {
            letter-spacing: -1px;
        }
}