li:hover{
    color: #eee;
    transition: .7s;
    transform: scale(1.3);
    cursor: pointer;
    border: none;

}
.moble-list{
    display: none;
}

.nav-hr{
    width: 450px;
    position: absolute;
    right: 100px;
    top: 50px;
    height: 0.99px;
    border: none;
    border-radius: 5px;
    background: rgb(0,38,78);
    /* background: linear-gradient(90deg, rgba(255,35,35,1) 10%, rgba(254,234,21,1) 51%, rgba(255,35,35,1) 89%); */
