}
@media only screen and (max-width: 800px) {
    .list-main{
        display: none;
    }
    .moble-list{
        display: block;
        z-index: 1;
        position: absolute;
        width:80%;                                                                                                                      
    }
    .none-mob{
        display: none;
    }
    .mob{
        height: 60vh;
        display: flex;
        flex-direction: column;
        background-color: black;
        animation-name: menus;
        border-radius: 5px;
        top: 0;
       animation-duration: 2s;
       position: absolute;
       width: 20vb;
       text-align: center;
       gap: 35px;
       padding: 45px;
    }
   .hu{
    left: 20px;
    position: absolute;
        z-index: 999999;
    }
    .none-mob{
        animation-name: close;
        position: relative;
        animation-duration: 2s;
    }
    
}
@keyframes close{
    to{
        right: -2550px;
    }
}
 @keyframes menus {
    from{
        left: -300px;
    }
    to{
        left: 10px;
    }
}

.animate-this-element {
  animation: name-of-animation 5s infinite;
}