::-webkit-scrollbar {
    width: 4px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow:  black; 
    border-radius: 80px;
    background-color: #0C0C0C;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #FFFFFF; 
    border-radius: 100px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: yellow; 
  }

  .scroll-contain {
    overflow:auto;
    scroll-snap-type: y mandatory;
  }
.section{
  scroll-snap-align: end;
}